@import url(https://fonts.googleapis.com/css?family=Roboto:500);

.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

h1{
  font-family:Verdana, Geneva, Tahoma, sans-serif;
}

p {
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  margin: 5px 0;
}


.alerts-container {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  position: fixed;
  top: 0px;
  left: 20px;
  width: 250px;
  z-index: 1000;
}

.charts-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-bottom: 20px; 
  padding-top: 20px;
  background-color: white;

}
@media (max-width: 768px)
{
  .charts-container {
    grid-template-columns: 1fr;
  } 
}

input[type="text"],
input[type="number"] {
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline:none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
  margin-bottom: 20px;
}

.city {
  width: 250px;
}

#city-search {
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 250px;
  position: absolute;
  top: 45px;
  z-index: 2;
  align-self: center;
  margin: 0;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions>li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions>li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions>li:hover {
  background-color: #bcdcdb;
}

.event {
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  border: none;
  border-radius: 8px;
  position: relative;
  text-align: center;
}

.details-button {
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  text-align: center;
  outline: none;
  cursor: pointer;
  background-color: #94b1ff;
}